const λ = (i) => {
  return (-Math.pow(μ[i], 2) * k) / Math.pow(R, 2) - (2 * α) / l;
};

const B = (i) => {
  if (i === 0) {
    if (R >= 0.2) {
      return (12 * β) / Math.pow(R, 2);
    } else {
      return 300 * β;
    }
  }
  return (
    (120 * β * J_n((μ[i] * 0.2) / R, 1)) /
    (R * Math.pow(J_n(μ[i], 0), 2) * μ[i])
  );
};

const U = (r, t) => {
  let result = 0;
  for (let i = 0; i <= n; i++) {
    const lambda = λ(i);
    result +=
      ((B(i) * (Math.pow(Math.E, (lambda * t) / c) - 1)) / lambda) *
      J_n((μ[i] * r) / R, 0);
  }
  return result;
};

const factorial = (x) => {
  let result = 1;
  if (x === 0) {
    return 1;
  }
  if (x < 0) {
    console.log("Беда факториал");
    return undefined;
  }
  for (let i = 2; i <= x; i++) {
    result *= i;
  }
  return result;
};
